body {
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 1.6rem;
  color: #fff !important; }

.text-center {
  text-align: center; }

.mb-big {
  margin-bottom: 6rem; }

.mb-small {
  margin-bottom: 1.5rem; }

body {
  padding: 0;
  background-color: #101015; }

a {
  color: inherit;
  text-decoration: none; }

html {
  font-size: 62.5%; }

img {
  max-width: 100%;
  height: auto; }

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0; }

.container {
  width: 1200px;
  margin: 0 auto;
  padding-left: 1rem;
  padding-right: 1rem; }
  .container::after {
    content: '';
    clear: both;
    display: table; }
  @media (max-width: 1200px) {
    .container {
      width: 900px; } }
  @media (max-width: 900px) {
    .container {
      width: 768px; } }
  @media (max-width: 768px) {
    .container {
      width: 100%;
      padding: 0; } }

.pagination {
  text-align: center;
  background-color: #fff;
  display: inline-block;
  border-radius: 5px;
  overflow: hidden; }
  .pagination__control--active {
    background-color: #FFD300 !important;
    color: #333 !important; }
  .pagination__control--done {
    background-color: #9292a2; }
  .pagination a,
  .pagination span {
    padding: 1rem 1.5rem;
    margin-left: -2px;
    margin-right: -2px;
    background-color: #fff;
    display: inline-block;
    color: #9292a2; }
  .pagination a:hover {
    background-color: #f1f1f1; }

.tag {
  color: #FFD300;
  background: #333;
  padding: 1rem 3rem;
  font-weight: 700;
  z-index: 2;
  border-radius: 15px; }
  .tag a {
    text-decoration: none; }

.container-card {
  display: flex;
  margin-top: 25vh;
  justify-content: space-between;
  flex-wrap: wrap; }

.card {
  background: #fff;
  height: 24rem;
  color: #333;
  border-radius: 4px;
  margin-bottom: 2rem;
  overflow: hidden;
  display: flex;
  flex-basis: 100%; }
  .card a {
    text-decoration: none;
    text-transform: uppercase;
    color: #333;
    transition: color 300ms ease-in-out; }
    .card a:hover {
      color: #1985A1;
      transition: color 300ms ease-in-out; }
  .card__picture {
    overflow: hidden;
    height: inherit;
    flex-basis: 30%;
    position: relative; }
    .card__picture a {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0; }
    .card__picture--img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 500ms ease;
      cursor: pointer; }
      .card__picture--img:hover {
        transform: scale(1.05);
        transition: transform 500ms ease; }
  .card__content {
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    flex-basis: 70%; }
    .card__content .data {
      font-size: 1.2rem;
      color: #9292a2; }
  .card__author {
    margin-right: 2rem; }
  .card__footer {
    border-top: 1px solid #e4e7ed;
    text-transform: uppercase;
    display: flex;
    justify-content: flex-end;
    margin-top: auto; }
    .card__footer span {
      position: relative; }
    .card__footer .fa-tags {
      color: #9292a2;
      margin-right: .5rem; }

.topbar {
  height: 40px;
  background-color: #101015; }
  .topbar__social {
    margin: .7rem 0;
    float: right; }
    .topbar__social a {
      text-decoration: none; }
      .topbar__social a:not(:last-child) {
        margin-right: 1rem; }
      .topbar__social a:hover {
        color: #FFD300; }

.menu-toggle {
  background: #2b1e2e;
  cursor: pointer;
  width: 40px;
  height: 40px;
  display: none;
  background: #ccc;
  border-radius: 50%;
  padding: 0 .3rem;
  position: absolute;
  right: 20px;
  top: 60px;
  overflow: hidden;
  z-index: 999; }
  .menu-toggle .hamburger {
    position: relative;
    display: block;
    width: 80%;
    height: 3px;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    color: #fff;
    background: #fff;
    transition: all 500ms ease-in-out; }
    .menu-toggle .hamburger::before, .menu-toggle .hamburger::after {
      content: '';
      position: absolute;
      color: #fff;
      background: #fff;
      height: 3px;
      width: 100%;
      border-radius: 15px;
      transition: all 500ms ease-in-out; }
    .menu-toggle .hamburger::before {
      transform: translateY(8px); }
    .menu-toggle .hamburger::after {
      transform: translateY(16px); }
  .menu-toggle.header-open .hamburger {
    left: 4px;
    top: 19px;
    transform: rotate(45deg); }
    .menu-toggle.header-open .hamburger::before {
      opacity: 0; }
    .menu-toggle.header-open .hamburger::after {
      transform: rotate(-90deg); }
  @media only screen and (max-width: 768px) {
    .menu-toggle {
      display: block; } }

.header {
  background-color: #333;
  text-transform: uppercase;
  font-weight: 700;
  transition: transform 500ms ease; }
  .header__logo {
    display: inline-flex;
    margin-left: 2rem;
    align-self: center;
    width: 32px; }
    .header__logo-valknut {
      fill: #FFD300;
      width: 100%; }
    @media only screen and (max-width: 768px) {
      .header__logo {
        display: none; } }
  .header__main-area {
    display: flex;
    height: auto;
    justify-content: space-between; }
  .header__navbar-nav {
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: center;
    height: 100%; }
    .header__navbar-nav-items {
      cursor: pointer;
      display: flex;
      text-align: center;
      position: relative; }
      .header__navbar-nav-items a {
        text-decoration: none;
        color: #fff;
        align-self: center;
        width: 100px;
        padding: 1rem 0; }
        @media screen and (min-width: 768px) {
          .header__navbar-nav-items a.active {
            border-top: solid 4px #E60000;
            background: #595959; } }
      @media screen and (min-width: 768px) {
        .header__navbar-nav-items:hover {
          transition: background-color 500ms;
          background: #595959; }
        .header__navbar-nav-items:hover::before {
          height: 4px; }
        .header__navbar-nav-items::before {
          transition: height 200ms;
          content: '';
          height: 0;
          position: absolute;
          background: #E60000;
          width: 100%; } }
  @media only screen and (max-width: 768px) {
    .header {
      transform: translateY(-60%);
      opacity: 0;
      visibility: hidden;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      z-index: 3; }
      .header [class^="header__"] {
        flex-direction: column; }
      .header.is-open {
        transform: translateY(0);
        visibility: visible;
        opacity: 1;
        background: rgba(117, 117, 117, 0.8);
        height: 100vh; }
      .header__main-area {
        height: 50vh;
        justify-content: center; }
      .header__navbar-nav-items {
        margin: 0 auto;
        width: 50%;
        border-bottom: 1px solid #fff;
        margin-top: 2rem;
        border-radius: 5px; } }

footer {
  margin-top: 40vh;
  background-image: linear-gradient(to top, #434343 -120%, black 40%);
  padding-top: 5rem;
  padding-bottom: 5rem; }

